  display: flex;
  flex-direction: column;
  gap: 10px;
}

.label-container {
  display: flex;
  gap: 10px;

  align-items: center;
}

.btn {
  border-radius: 4px;
  width: 100px;
  height: 40px;
  background-color: black;
  border-color: black;

  color: white;
  opacity: 0.5;
  font-size: 16px;

  &:hover {
    opacity: 1;
    cursor: pointer;
  }
}

